@import url('https://fonts.googleapis.com/css2?family=Leckerli+One&family=Montserrat+Alternates:ital,wght@0,500;0,700;1,500;1,700;1,900&display=swap');


.form {
    background-color: #222253;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 0 0;
    margin: 0;
  
   
}

.formGroups {
    width: 900px;
    height: auto;
    display: flex;
    border-radius: 20px;
    background-color: #eeeeee;
    padding: 20px 15px;
    box-shadow: 5px 10px #090909;
    
  
}

.formLeft {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.formRight {
    flex: 1;
}

.formsSubTitle {
    font-size: 20px;
    letter-spacing: 1.3px;
    margin-bottom: 10px;
    font-family: 'Leckerli One', cursive;
}
.social{
    color: #ffffff;
    text-shadow: 2px 2px #000000;
    font-size: 60px;
   
    font-family: 'Montserrat Alternates', sans-serif;
    
}
.formsTitle {
    text-shadow: 2px 2px #05ff0d;
    margin: 30px 0;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-size: 60px;
    font-family: 'Montserrat Alternates', sans-serif;
}

.formGroup {
    margin-bottom: 30px;
}

.formGroup input {
    height: 40px;
    border: none;
    outline: none;
    padding: 0 10px;
    width: 100%;
    border-radius: 10px;
}

.form-btnLogin button {
    display: block;
    margin: 0 auto;
    padding: 5px 20px;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    background-color: #4141ff;
    color: #ffffff;
    font-size: 18px;
    border-radius: 10px;
    transition: all 0.5s;
    letter-spacing: 1.3px;
}

.form-btnLogin button:hover {
    background-color: #ffffff;
    color: #4141ff;
}

.form-btnRegister {
    margin-top: 80px;
}

.form-btnRegister a {
    display: block;
    margin: 0 auto;
    width: max-content;
    padding: 5px 20px;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    background-color: #01ff2c;
    color: #ffffff;
    font-size: 18px;
    border-radius: 10px;
    transition: all 1s;
    letter-spacing: 1.3px;
}

.form-btnRegister a:hover {
    background-color: #ffffff;
    color: #01ff2c;
}